import React from 'react'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Container from '../../components/Container'
import Image from '../../components/Image'
import { responsive } from '../../components/ThemeProvider/theme'

import five from './five.svg'
import abundant from './abundant.svg'
import income from './income.svg'
import power from './power.svg'
import five1 from './five1.svg'
import five2 from './five2.svg'
import abundant1 from './abundant1.svg'
import power1 from './power1.svg'
import income1 from './income1.svg'
import income2 from './income2.svg'
import pig from './pig.svg'
import people from './people.svg'

const secs = [
  {
    color: 'green',
    icon: five,
    bigTitle: '5',
    title: '5大資產',
    desc: '布局5大收益資產，靈活配置4+1的比例，打造全週期的收益組合',
    pictures: [five1, five2],
    source: '*本基金之資產配置為根據目前市場狀況所預計之配置，待基金正式成立進場投資時，仍可能因市場環境的變化而有所調整。資料來源：施羅德投信，2020/12/31 。'

  },
  {
    color: 'yellow',
    icon: power,
    bigTitle: '力',
    title: '力好雙贏',
    desc: '兼顧收益與波動的債券，為投組基石。順應市場輪動週期，以高收益債掌收益，高評等控風險，順勢調配。',
    pictures: [power1],
    source: '資料來源：施羅德投信，2020/12/31。'
  },
  {
    color: 'lightRed',
    icon: abundant,
    bigTitle: '豐',
    title: '豐富主題',
    desc: '適度調整股票部位，參與市場全週期動能，增加基金收益來源。適時加入趨勢主題投資，再添收益潛力。',
    pictures: [abundant1],
    source: '資料來源 : 施羅德投資，2020/12/31。',
    afterNote: '上述主題僅作為舉例說明，不代表任何金融商品之推介或投資建議。'
  },
  {
    color: 'purple',
    icon: income,
    bigTitle: '收',
    title: '收益加值',
    desc: '兼顧收益與波動的債券，為投組基石。順應市場輪動週期，以高收益債掌收益，高評等控風險，順勢調配。',
    pictures: [income1, income2],
    source: '資料來源：Bloomberg，施羅德投信，計算2007/10/31~2020/12/31不同指數組合之累積報酬率，截至2020/12/31。',
    beforeNote: '*以簡單平均計算，計算自 2007/10/31 起開始投資之平均年報酬率 **使用市場指數：摩根大通新興市場企業債指數、巴克萊全球高評等債券指數、iBoxx美元高收益債券指數、全球股票指數，僅作為資產配置投資說明，未考慮任何申購與贖回之影響因素，亦未考慮任何交易所產生的費用，以上僅為歷史指數資料模擬投資之結果，不代表本投資組合之實際報酬率及未來績效保證，不同時間進行模擬操作，其結果亦可能不同。'
  },
]

const NoteText = ({ children, ...props }) => (
  <Text.Inline color="noteGreen" fontWeight="bold" {...props}>{children}</Text.Inline>
)

const Sections = () => {
  return (
    <Container id="fund-head" pt={responsive('2em', '3em')}>
      <Box borderBottom="2px solid" borderColor="darkBlue" fontWeight="bold">
        {secs.map(({ color, bigTitle }) => (
          <Text.Inline fontSize={responsive('2em', '3em')} color={color}>{bigTitle}</Text.Inline>
        ))}
        <Text.Inline ml="0.125em" fontSize={responsive('1.625em', '2.5em')} color="darkBlue">把握全週期收益契機</Text.Inline>
      </Box>
      <Box mt="0.5em" fontWeight="bold">
        <Text fontSize={responsive('1em', '1.25em')} color="darkBlue">施羅德打造以收益為主的多重資產基金</Text>
        <Text fontSize={responsive('1em', '1.25em')} color="darkBlue">收益債券＋主題股票，實現更靈活、低波動的投資策略</Text>
      </Box>
      {secs.map(({ color, icon, title, desc, pictures, source, afterNote, beforeNote }, index) => (
        <Box position="relative" pt={index ? responsive('3em', '5em') : '1em'} pb="1em" key={index}>
          <Flex alignItems="center">
            <Box width={responsive('2.5em', '4em')} mr="0.5em">
              <Image src={icon} />
            </Box>
            <Box
              flex={1}
              borderBottom="1px solid black"
              fontWeight="bold"
              position="relative"
            >
              <Text color={color} fontSize={responsive('1.5em', '2.5em')}>{title}</Text>
              <Box position="absolute" left="0" right="0" bottom="-0.5em" transform="translateY(100%)" fontSize={responsive('1em', '1.25em')} mt="0.5em">
                {desc}
              </Box>
            </Box>
            {index === 1 && (
              <Box width={responsive(0, '15%')} pl="1em">
                <Image src={people} />
              </Box>
            )}
          </Flex>
          <Box pl={responsive('0', '4.5em')} pt="1em">
            <Flex py="3em" flexDirection={responsive('column', 'row')}>
              {pictures.map((src, k) => (
                <Box width="100%" pt="1em" key={k}>
                  <Image src={src} />
                </Box>
              ))}
            </Flex>
            <Box width={!Boolean(index) && responsive('100%', '85%')} fontSize={responsive('0.75em', '1em')}>
              <NoteText>{afterNote}</NoteText>
              {source}
              <NoteText>{beforeNote}</NoteText>
            </Box>
          </Box>
          {!Boolean(index) && (
            <Box width={responsive(0, '10%')} position="absolute" right={responsive(0 , '1em')} bottom="-2em">
              <Image src={pig} />
            </Box>
          )}
        </Box>
      ))}
    </Container>
  )
}

export default Sections
