import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import Container from '../../components/Container'
import Image from '../../components/Image'

import info from './info.svg'
import coins from './coins.svg'
import { responsive } from '../../components/ThemeProvider/theme'

const Info = () => {
  return (
    <Container py="3em">
      <Image src={info} />
      <Box py="2em" fontSize={responsive('0.75em', '1em')}>
        *依中華民國證券投資信託暨顧問商業同業公會公布「基金風險報酬等級分類標準 (基金風險報酬等級由低至高區分為 RR1、RR2、RR3、RR4、RR5 五個等級)，故本基金風險報酬等級為 RR3。提醒投資人此等級分類，基於一般市場狀況反映市場價格波動風險，無法涵蓋所有風險，不宜作為投資唯一依據，投資人仍應注意所投資基金個別的風險。資料來源：施羅德投信。
      </Box>
      <Image src={coins} />
      <Box py="2em">
        <Text.Inline color="noteGreen" fontSize={responsive('0.75em', '1em')}>
          **投資遞延手續費(後收手續費)NA類型 (N累積)及NB類型(N配息)各計價類別受益權單位之受益人，其手續費之收取將於買回時支付，且該費用將依持有期間而有所不同，其餘費用之計收與前收手續費類型完全相同，亦不加計分銷費用，請詳閱公開說明書壹、九、(二)、受益人應負擔費用之項目及其計算、給付方式。
        </Text.Inline>
        <Text fontSize={responsive('0.75em', '1em')}>基金配息率不代表基金報酬率，且過去配息率不代表未來配息率，投資人於獲配息時，宜一併注意基金淨值的變動；基金淨值可能因市場因素而上下波動。各相關配息時間依基金管理機構通知之實際配息日期為準；實際配息入帳日以銷售機構作業時間為準。固定收益分配類型基金會定期將基金收益分配予投資人，投資人應當了解依其原始投資日期之不同，基金的配息可能由基金的收益或本金中支付。任何涉及由本金支出的部份，可能導致原始投資金額減損。本基金配息前未先扣除應負擔之相關費用。配息可能因利息收入、股息收入、權利金收入或其他可分配收入尚未進帳或短期間不足支付配息，而有部分配息來自於本金的風險。基金配息之年化配息率計算公式為「每單位配息金額÷除息日前一日之淨值×一年配息次數×100%」，且年化配息率為估算值。基金配息組成項目表已揭露於施羅德網站，投資人可至http://www.schroders.com.tw查詢。提醒投資人配息並非固定不變或保證獲利，配息類股除息後淨值將隨之下降，配息可能影響再投資的複利效益。</Text>
      </Box>
    </Container>
  )
}

export default Info
